@import 'variables.scss';
.hero {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  background-color: $primary-red;
  background-image: repeating-linear-gradient(45deg, transparent, transparent 80px, rgba(160,2,2,.5) 80px, rgba(160,2,2,.5) 160px);
  color: white;

  .main {
    max-width: 600px;
  }

  h1, h2, h3 {
    color: #D6E9E5;
    font-weight: 300;
    font-size: 45px;
    text-align: center;
  }

  h1.logo {
    margin-bottom: 40px;
  }

  h2 {
    font-size: 50px;
  }

  h3 {
    font-size: 30px;
  }

  .divider {
    align-self: center;
    width: 0px;
    height: 2px;
    margin: 60px 0;
    background-color: white;
    transition: width 1.5s ease-in-out;
  }
  .divider.show {
    width: 100%;
  }

  .roller {
    transition: all 1.5s ease-in-out;
    max-width: 600px;
  }

  .show-interest-button {
    background-color: white;
    color: $primary-red;
    border-radius: 7.5px;
    font-size: 30px;
    line-height: 60px;
    margin: 35px auto;
    transition: box-shadow .1s ease-in-out;
    text-align: center;
    width: 300px;
    height: 60px;

    p {
      font-weight: 300;
    }
  }

  .right-side.show {
  }
  .right-side-content.hidden {
    margin-top: 100vh;
  }
  .right-side-content.show-form {
    margin-top: -160px;
  }
}

@media (max-width: 868px){
  .section.hero {
    padding: 20px;

    h1.logo, h2 {
      color: #D6E9E5;
      font-weight: 300;
      font-size: 35px;
      text-align: center;
    }
    h2 {
      font-size: 20px;
    }
    h3 {
      font-size: 14px;
    }
  }
}
